<template>
  <v-container fluid>
    <v-data-table
      :headers="headers"
      :items="records"
      :search="search"
      class="elevation-2"
      :loading="loadingRecords"
      loading-text="Loading... Please wait"
    >
      <template v-slot:top>
        <div class="d-flex mx-4 pt-4 mb-4">
          <v-spacer></v-spacer>
          <v-text-field
            v-model="search"
            append-icon="mdi-magnify"
            label="Search"
            outlined
            dense
            hide-details
            :disabled="loadingRecords"
          ></v-text-field>
        </div>
        <v-divider></v-divider>
      </template>

      <template v-slot:[`item.keyword`]="{ item }">
        <div class="text-truncate" style="max-width: 200px">
          {{ item.keyword }}
        </div>
      </template>

      <template v-slot:[`item.remarks`]="{ item }">
        <div class="text-truncate" style="max-width: 400px">
          {{ item.remarks }}
        </div>
      </template>

      <template v-slot:[`item.created_at`]="{ item }">
        {{ setDateFormat(item.created_at) }}
      </template>

      <template v-slot:[`item.action`]="{ item }">
        <div v-show="$vuetify.breakpoint.mdAndUp ? true : false">
          <v-tooltip left>
            <template v-slot:activator="{ on, attrs }">
              <v-btn icon small color="primary" @click="showSolution(item)">
                <v-icon small color="primary" v-bind="attrs" v-on="on">
                  mdi-eye
                </v-icon>
              </v-btn>
            </template>
            <span>View</span>
          </v-tooltip>
        </div>

        <div v-show="$vuetify.breakpoint.mdAndUp ? false : true">
          <v-btn color="primary" x-small @click="showSolution(item)">
            View
          </v-btn>
        </div>
      </template>
    </v-data-table>

    <v-dialog v-model="viewDialog" width="500">
      <v-card>
        <v-card-title> {{ keyword }} </v-card-title>

        <v-card-text>
          <p><strong>Remarks:</strong> {{ remarks }}</p>
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn class="text-capitalize" @click="viewDialog = false">
            Close
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-container>
</template>

<script>
import { mapState } from "vuex";
import moment from "moment";

export default {
  data() {
    return {
      search: "",
      headers: [
        { text: "Keyword", value: "keyword" },
        { text: "Remarks", value: "remarks" },
        { text: "Date Created", value: "created_at" },
        { text: "Action", value: "action", sortable: false },
      ],
      keyword: null,
      remarks: null,
      viewDialog: false,
    };
  },

  created() {
    this.getRecords();
  },

  computed: {
    ...mapState("solution", ["records", "loadingRecords"]),
  },

  methods: {
    getRecords() {
      this.$store.commit("solution/setRecords", []);
      this.$store.dispatch("solution/getRecords");
    },

    showSolution(val) {
      this.viewDialog = true;
      this.keyword = val.keyword;
      this.remarks = val.remarks;
    },

    setDateFormat(date) {
      return moment(date).format("MMM D, YYYY h:mm A");
    },
  },
};
</script>